<template>
  <h1 style="text-align: center">{{ booksJson[0].Name }}</h1>
  <div class="bookContainer">
    <div class="item bookImg" style="text-align: center">
      <img :src="require('../assets/bookImages/' + booksJson[0].img)" />
    </div>
    <div class="item bookDetails">
      <ul>
        <li><span class="label">Author:</span> {{ booksJson[0].Author }}</li>
        <li><span class="label">ISBN:</span> {{ booksJson[0].ISBN }}</li>
        <li><span class="label">Release:</span> {{ booksJson[0].Release }}</li>
        <li>
          <span class="label">Publishing House:</span>
          {{ booksJson[0].PublishingHouse }}
        </li>
      </ul>
      <button v-if="userID" @click.prevent="addToCart($event)" class="cart-button">
        <a href="#" @click.prevent="addToCart($event)" class="add-to-cart">{{
          $t("addToBasket")
        }}</a>
        <a href="#" @click.prevent="addToCart($event)" class="added">{{
          $t("itemAdded")
        }}</a>
        <i class="fa fa-plus"></i> <i class="fa fa-circle-thin"></i>
        <i class="fa fa-check"></i>
      </button>
      <div v-else class="alert alert-danger alert-link">
        {{ $t("logInForShoppingCart") }}
      </div>
    </div>
  </div>
</template>

<script>
import BooksService from "../services/BooksService";
import UsersService from "../services/UsersService";

export default {
  name: "BooksDetails",
  data() {
    return {
      booksJson: "",
      userID: null,
    };
  },
  async beforeCreate() {
    try {
      this.booksJson = await BooksService.getBooksDetails(this.$route.params.bookid);
    } catch (err) {
      this.error = err.message;
      console.error(err);
    }
    this.userID = UsersService.getUserIDFromLocalStorage();
  },
  methods: {
    addToCart(event) {
      let btn = event.target;
      while (btn.localName !== "button") {
        btn = btn.parentElement;
      }
      btn.classList.add("clicked");
    },
  },
};
</script>

<style>
.bookContainer {
  margin: 70px 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.item {
  flex-grow: 0;
  flex-shrink: 0;
  flex: 1 0 21%; /* explanation below */
  text-align: left;
  padding-top: 15px;
}

.item + .item {
  margin-left: 2%;
}

.label {
  font-size: 1.3rem;
  font-weight: 700;
}

.bookDetails {
  font-size: 1.2rem;
}

.bookDetails ul {
}
</style>

<style scope>
.cart-button {
  position: relative;
  width: 500px !important;
  outline: 0;
  background-color: #e8630a;
  color: #fff;
  border: none;
  height: 48px;
  width: 200px;
  padding: 10px;
  border-radius: 10px;
  line-height: 0px;
  overflow: hidden;
}

.cart-button:focus {
  outline: none !important;
}

.cart-button .fa-plus {
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 22%;
  font-size: 1.2em;
  transform: translate(-50%, -50%);
}

.cart-button .fa-circle-thin {
  position: absolute;
  z-index: 1;
  top: 50%;
  left: -20%;
  font-size: 1.5em;
  transform: translate(-50%, -50%);
}

.cart-button .fa-check {
  position: absolute;
  z-index: 1;
  top: -52%;
  left: 20%;
  font-size: 0.7em;
  transform: translate(-50%, -50%);
}

.cart-button a {
  position: absolute;
  left: 50%;
  top: 50%;
  color: #fff;
  transform: translate(-50%, -50%);
  font-size: 16px;
}

.cart-button a.added {
  opacity: 0;
}

.cart-button.clicked .fa-plus {
  animation: addicon 2s ease-in forwards;
}

.cart-button.clicked {
  animation: color 2s ease-in forwards;
}

.cart-button.clicked .fa-circle-thin {
  animation: circle 1s ease-in forwards;
}

.cart-button.clicked .fa-check {
  animation: check 2s ease-in forwards;
}

.cart-button.clicked a.add-to-cart {
  animation: addcart 2s ease-in forwards;
}

.cart-button.clicked a.added {
  animation: added 2s ease-in forwards;
}

@keyframes addicon {
  0% {
    opacity: 1;
  }

  30%,
  100% {
    opacity: 0;
  }
}

@keyframes circle {
  0% {
    left: -10%;
  }

  100% {
    left: 20%;
  }
}

@keyframes check {
  0%,
  40% {
    top: -20%;
  }

  60% {
    top: 48%;
  }

  100% {
    top: 48%;
  }
}

@keyframes addcart {
  0%,
  30% {
    opacity: 1;
  }

  30%,
  100% {
    opacity: 0;
  }
}

@keyframes added {
  0%,
  80% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes color {
  0% {
    background-color: #e8630a;
  }

  80%,
  100% {
    background-color: #2b4353;
  }
}
</style>
